}
input#isexpanded {
  display: none;
}
label#expand-btn,
label#expand-btn:after,
label#expand-btn:before {
  width: 30px;
  height: 5px;
  background-color: var(--color);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 2px;
}
label#expand-btn {
  position: absolute;
  top: 30px;
  right: 15px;
  z-index: 3;
}
label#expand-btn:after,
label#expand-btn:before {
  content: "";
  position: absolute;
  left: 0;
  top: -9px;
}
label#expand-btn:after {
  top: 9px;
}
#isexpanded:checked ~ #expand-btn {
  background-color: transparent;
}
#isexpanded:checked ~ #expand-btn:after {
  -webkit-transform: rotate(-45deg) translate(7px, -8px);
  transform: rotate(-45deg) translate(7px, -8px);
}
#isexpanded:checked ~ #expand-btn:before {
  -webkit-transform: rotate(45deg) translate(5px, 5px);
  transform: rotate(45deg) translate(5px, 5px);
}
#isexpanded:checked ~ * #block-mainmenu {
  left: -1px;
}
