/**
 * breadcrumbs
 *
 */
 #block-konkin-breadcrumbs {
     color: #999;
    ol {
        margin: 15px 0;
        list-style: none;
        
    }

    li {
        display: inline-block;
        a:after {
            content: '»';
            padding-left: 3px;
            text-decoration: none;
            display: inline-block;
        }
    }
} 